import { writable } from "svelte/store";
export const cardData = writable(undefined);
// Fetch the data from the window, or fallback to the example data file
export const setCardData = (cardDataId) => {
    try {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-argument
        const data = JSON.parse(atob(window.__MF_DATA__[cardDataId]));
        cardData.set(data);
    }
    catch (error) {
        // for now, we are loading an example card if there is no string
        fetch("/card-example.json")
            .then((resp) => resp.json())
            .then((data) => {
            cardData.set(data);
        })
            .catch(console.error);
    }
};
export const modal = writable(undefined);
